import React from 'react';
import { Carousel, Image, img } from 'react-bootstrap';
import './Slider.css';

const Slider = () => {
    return (
        <Carousel variant="dark">
            <Carousel.Item>
                <img
                    className=" w-100"
                    src='https://explore-work.com/wp-content/uploads/2018/03/feature_ready-set-go.jpg' alt=""
                    alt="First slide"
                />
                <Carousel.Caption>

                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <Image
                    className=" w-100"
                    src="https://explore-work.com/wp-content/uploads/2018/03/feature_work-experiences.jpg"
                    alt="Third slide"
                ></Image>
                <Carousel.Caption>
                    <h5>Rise Your Hand</h5>
                    <p>For success</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className=" w-100"
                    src="https://explore-work.com/wp-content/uploads/2018/03/feature_careerpath.jpg"
                    alt="Third slide"
                />
                <Carousel.Caption>
                    <h5>Join Our Offer Courses</h5>
                    <p>Be Skils Be Prepaired for Jobs</p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    );
};

export default Slider;